import React from 'react';
import { SpotifyContext } from "./../context/SpotifyProvider";

const ShowSpotify = () => {

    const {spotify, setSpotify} = React.useContext(SpotifyContext);

    const handlerClick = () =>{
        setSpotify(!spotify);
    }

    return !spotify? (
      <div className="header_spot_options pointer" onClick={handlerClick}>
        <div className="media_icon_spot">
          <i className="fas fa-headphones-alt fa-item" onClick={() => {}}></i>
        </div>
      </div>
    ) : (
      <>
      <div className="header_spot_options pointer" onClick={handlerClick}>
        <div className="media_icon_spot">
          <i className="fas fa-headphones-alt fa-item orange" onClick={() => {}}></i>
        </div>
      </div>
      
      </>
    );
}

export default ShowSpotify;
